.question-container {
  width: 400px;
}

.options-container {
  padding: 16px;
  max-width: 400px;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

@media only screen and (max-width: 600px) {
  .options-container {
    max-width: 100%;
  }
}

.option {
  flex-grow: 1;
  padding: 0.5rem 1rem;
  min-width: fit-content;
  font-size: medium;
  background-color: rgb(247, 247, 247);
  border: 0.5px solid rgb(169, 169, 169);
  border-radius: 16px;
  color: rgb(59, 59, 59);
}

.option:last-of-type {
  max-width: max-content;
}

.option:hover {
  background-color: rgb(247, 247, 247);
}

.option.selected {
  color: black;
  border: 2px solid black;
}

/* Options in Stats */

.option.correct {
  background-color: rgba(0, 128, 0, 0.2);
}

.option.wrong {
  background-color: rgba(255, 0, 0, 0.2);
}

.action-btn {
  font-size: small;
  font-weight: 500;
  margin-top: 16px;
  background-color: black;
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 32px;
  border: none;
}

.action-btn:hover {
  background-color: rgb(43, 43, 43);
  color: gray;
}

.action-btn:disabled {
  background-color: rgb(151, 151, 151);
  color: rgb(201, 201, 201);
}